.titleBar{background:#18181a;width:100%;height:50px;box-sizing:border-box;display:flex;color:white}.titleBar .left{flex-grow:1;display:flex;overflow:hidden}.titleBar .left .logo{height:100%}.titleBar .left .tabs{display:flex;overflow:hidden}.titleBar .left .tabs .tab{display:flex;margin-top:10px;border-top:solid 2px transparent;height:40px;width:0;-webkit-animation:tabOpen 0.4s forwards;animation:tabOpen 0.4s forwards;transition:0.1s;overflow:hidden;padding:0 10px;box-sizing:border-box}.titleBar .left .tabs .tab:not(.active):hover{background:#1e1e25;border-color:#252530}.titleBar .left .tabs .tab.active{background:#252530}.titleBar .left .tabs .tab img{height:20px;width:20px;margin:10px 0;margin-right:10px;flex-shrink:0}.titleBar .left .tabs .tab h1{font-weight:400;font-size:15px;overflow:hidden;margin:auto 0;white-space:nowrap;flex-grow:1}.titleBar .left .tabs .tab .close{margin:auto 0 auto 5px;border-radius:50%;padding:8px;font-size:12px;transition:0.1s}.titleBar .left .tabs .tab .close:hover{background:#303040}.titleBar .left .tabs .new{display:flex;flex-flow:column;justify-content:center;text-align:center;height:40px;width:40px;margin-top:10px;border-top-right-radius:5px;transition:0.1s;flex-shrink:0;box-sizing:border-box}.titleBar .left .tabs .new:hover{background:#1e1e25}.titleBar .right{display:flex;flex-shrink:0}.titleBar .right div{width:50px;height:50px;text-align:center;display:flex;flex-flow:column;justify-content:center;transition-duration:0.1s}.titleBar .right div:hover{background:#ffffff20}.titleBar .right div.close:hover{background:#ff0000c0}.titleBar .right .user{height:calc(100% - 10px);border-radius:50%;margin:5px;margin-right:15px}@-webkit-keyframes tabOpen{from{width:0}to{width:350px}}@keyframes tabOpen{from{width:0}to{width:350px}}.navBar{background:#252530;width:100%;height:40px;box-sizing:border-box;display:flex;color:white}.navBar .navButtons{display:flex}.navBar .navButtons i{margin:auto 0 auto 20px;display:block;border-radius:50%;padding:8px;font-size:12px;transition:0.1s}.navBar .navButtons i:hover{background:#303040}.navBar .navButtons i.refresh.active{-webkit-animation:spin 0.5s;animation:spin 0.5s}.navBar .inputBar{background:#1e1e25;flex-grow:1;margin:7px 20px;display:flex;overflow:hidden;cursor:text}.navBar .inputBar form{margin:0;width:100%;height:100%}.navBar .inputBar input{border:none;background:none;width:100%;height:100%;color:white;padding:0 10px;font-size:1em}.navBar .inputBar .lock{margin:auto 10px;font-size:20px}.navBar .inputBar .lock.secure{color:#00cc29}.navBar .inputBar .lock.insecure{color:#f70f0f}.navBar .inputBar .url{margin:auto 0px;white-space:nowrap}.navBar .inputBar .url .protocol{color:#808080}.navBar .inputBar .url .protocol.protocol-http{color:#f70f0f}.navBar .inputBar .url .protocol.protocol-https{color:#00cc29}.navBar .inputBar .url .port{color:#808080}.navBar .inputBar .url .pathname{color:#d0d0d0}.navBar .inputBar .url .search{color:#d0d0d0}.navBar .inputBar .url .hash{color:#808080}@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}body{margin:0;-webkit-user-select:none;cursor:default}body,input{font-family:Rubik}.app{width:100%;height:100%}input:focus{outline:none}

